@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

* {
    box-sizing: border-box;
}

.Contact {
    width         : 475px;
    margin        : 0 auto;
    padding-top   : 60px;
    padding-bottom: 60px;
}

.Contact>form>div>label,
.Contact>form>div>input,
textarea {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size  : 1.1rem;
}

input[type=text],
input[type=email],
select,
textarea {
    width        : 100%;
    padding      : 12px;
    border       : 1px solid #ccc;
    border-radius: 4px;
    box-sizing   : border-box;
    margin-top   : 6px;
    margin-bottom: 16px;
    resize       : vertical;
}

.container {
    border-radius   : 5px;
    background-color: #f2f2f2;
    padding         : 20px;
}

@media screen and (max-width: 500px) {
    .Contact {
        width: 70%;
    }
}