@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
    display        : flex;
    flex-direction : column;
    justify-content: space-between;
}



footer {
    height: 0px;
}

.footer {
    display        : flex;
    justify-content: flex-end;
    align-items    : center;
}

.socials,
img {
    margin: auto;
}

.socials {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size  : 10px;
    color      : #24252A;
    list-style : none;
}

.socials li {
    padding: 3px 2vh;
}
* {
    box-sizing: border-box;
    margin    : 0;
    padding   : 0;
}

a,
button {
    font-family    : 'Montserrat', sans-serif;
    color          : #24252A;
    text-decoration: none;
}

.title-container {
    width          : 100%;
    display        : flex;
    justify-content: center;
    padding        : 70px 0px;
    border-bottom  : 1px solid rgba(0, 0, 0, 0.1);
}

.title {
    font-size    : 15vw;
    font-family  : 'Montserrat', sans-serif;
    font-weight  : 600;
    color        : #24252A;
    text-align   : center;
    padding      : 10px 0px;
    /* background: red; */
}

.title-div {
    z-index    : 3;
    font-weight: 500;
    font-size  : 30px;
    width      : 30%;
}

.header-container {
    display        : flex;
    align-items    : center;
    justify-content: space-between;
    padding        : 0px 40px 0px;
    height         : 130px;
}

.menu-div {
    width    : 40%;
    font-size: 20px;
}

.menu-box {
    display        : flex;
    list-style     : none;
    justify-content: space-around;
}

.service-div {
    width          : 30%;
    display        : flex;
    justify-content: space-around;
    font-size      : 20px;
}



/*  */

#menu-toggle {
    opacity: 0;
    display: none;
}

#menu-toggle:checked+.menu-btn>span {
    transform: rotate(45deg);
}

#menu-toggle:checked+.menu-btn>span::before {
    top      : 0;
    transform: rotate(0deg);
}

#menu-toggle:checked+.menu-btn>span::after {
    top      : 0;
    transform: rotate(90deg);
}

#menu-toggle:checked~.menu-box {
    left: 0 !important;
}

@media (max-width: 1250px) {

    .title-div {
        width: 50%;
    }

    .menu-toggle {
        display: block;
    }

    .menu-div {
        width          : 50%;
        display        : flex;
        justify-content: end;
        padding-right  : 40px;
    }

    .service-div {
        display: none;
    }

    .menu-btn>span,
    .menu-btn>span::before,
    .menu-btn>span::after {
        display            : block;
        position           : absolute;
        height             : 4px;
        width              : 30px;
        background-color   : black;
        transition-duration: .25s;
        z-index            : 3;
    }

    .menu-btn>span::before {
        content: '';
        top    : -8px;
    }

    .menu-btn>span::after {
        content: '';
        top    : 8px;
    }

    .menu-box {
        z-index            : 2;
        display            : flex;
        flex-direction     : column;
        align-items        : center;
        justify-content    : center;
        position           : fixed;
        top                : 0;
        left               : -100%;
        width              : 100%;
        height             : 100%;
        background-color   : #dde5e9;
        transition-duration: .25s;
    }

    .menu-box>li {
        font-size  : 1.5rem;
        font-weight: 400;
        padding    : 0px 0px 30px 0px
    }
}
.loader {
    display          : flex;
    justify-content  : center;
    align-items      : center;
    position         : fixed;
    width            : 100%;
    height           : 100%;
    top              : 0;
    left             : 50%;
    transform        : translate(-50%, 0);
    z-index          : 9999;
    background       : #FFFFFF;
}

* {
    box-sizing: border-box;
    margin    : 0;
    padding   : 0;
}

.info-div {
    display        : flex;
    justify-content: center;
    align-items    : center;
    min-height     : 76vh;
}

/*이미지영역*/
.info-image {
    flex-basis: 50%;
    /* padding: 1%; */
}

/*텍스트 영역*/
.info-written {
    flex-basis: 45%;
    text-align: center;
}

.info-title {
    padding-bottom: 50px;
}

.info-title>span {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size  : 3.3vh;
}

.info-content>div {
    padding: 4px;
}

.info-content>div>span {
    font-family  : 'Montserrat', sans-serif;
    font-weight  : 300;
    font-size    : 2.3vh;
    margin-bottom: 10px;
}

@media screen and (max-width: 1300px) {
    .info-image {
        display: none;
    }

    .info-title {
        padding-top: 50px;
    }

    .info-title>span {
        font-weight: 500;
        font-size  : 23px;
    }

    .info-written {
        flex-basis: 100%;
    }

    .info-content>div>span {
        font-size: 15px;
    }
}
.brand-list-box {
    display    : flex;
    flex-wrap  : wrap;
    align-items: center;
    padding    : 0 10%;
}

.brand-list-logo {
    flex-basis: 33.3%;
    padding   : 10%;
}
* {
    box-sizing: border-box;
}

.Contact {
    width         : 475px;
    margin        : 0 auto;
    padding-top   : 60px;
    padding-bottom: 60px;
}

.Contact>form>div>label,
.Contact>form>div>input,
textarea {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size  : 1.1rem;
}

input[type=text],
input[type=email],
select,
textarea {
    width        : 100%;
    padding      : 12px;
    border       : 1px solid #ccc;
    border-radius: 4px;
    box-sizing   : border-box;
    margin-top   : 6px;
    margin-bottom: 16px;
    resize       : vertical;
}

.container {
    border-radius   : 5px;
    background-color: #f2f2f2;
    padding         : 20px;
}

@media screen and (max-width: 500px) {
    .Contact {
        width: 70%;
    }
}
.details_box {
    position      : relative;
    width         : 100%;
    height        : 0;
    padding-bottom: 56.25%;
}

.details_box>.details_iframe>iframe {
    display  : block;
    margin   : 0 auto;
    position : absolute;
    top      : 40%;
    left     : 50%;
    transform: translate(-50%, -50%);
    width    : 70%;
    height   : 70%;
}
.imageBox {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.image {
    position: relative;
    flex-shrink: 0;
    width: 33.3%;
    box-sizing: border-box;
    overflow: hidden; /* 자식 요소가 부모의 경계를 벗어나지 않도록 설정 */
}

.image__img {
    display: block;
    width: 100%;
    object-fit: cover;
    height: 340px;
    transition: filter 0.3s ease; /* 이미지 흐리게 효과를 위한 트랜지션 추가 */
}

.image__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    color: white; /* 기본 텍스트 색상 */
    font-family: 'Noto Sans KR', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.3s ease, background-color 0.3s ease; 
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7); 
}

.image:hover .image__img {
    filter: blur(5px); /* 호버 시 이미지 흐리게 */
}

.image__title {
    font-size: 2em;
    font-weight: bold;
}

.image__description {
    font-size: 1.25em;
    text-align: center;
    padding: 1em;
}

@media screen and (max-width: 1500px) {
    .image {
        width: 50%;
    }
}

@media screen and (max-width: 1000px) {
    .image {
        width: 100%;
    }
}

.image__overlay:hover {
    opacity: 1;
}

.image.light .image__overlay {
    background-color: rgba(255, 255, 255, 0.6); /* 밝은 흰색 오버레이 */
    color: black; /* 텍스트를 검정색으로 설정 */
    text-shadow: 2px 2px 4px rgba(255, 255, 255, 0.7); /* 밝은 텍스트 그림자 효과 */
}

.category-container {
    position  : relative;
    display   : flex;
    transition: all 0.15s linear;
}


.tabCloseBtn {
    display        : flex;
    flex-direction : column;
    justify-content: center;
    align-items    : center;

    position        : absolute;
    top             : 85px;
    margin-left     : 200px;
    z-index         : 1;
    width           : 50px;
    height          : 170px;
    background-color: orange;
    font-family     : 'Montserrat', sans-serif;
    font-size       : 25px;
    font-weight     : bold;
}

.centered {
    display        : flex;
    justify-content: center;
    align-items    : center;
    height         : 100vh;
    text-align     : center;
    font-size      : 2rem;
    font-weight    : bold;
}
